/**
 * Systme de limitation de dbit ct client
 * vite les erreurs HTTP 429 en limitant le nombre de requtes par seconde
 */

class RateLimiter {
    constructor(maxRequests = 10, timeWindow = 1000) {
        this.maxRequests = maxRequests; // Nombre max de requtes
        this.timeWindow = timeWindow;   // Fentre de temps en ms
        this.requests = [];             // Historique des requtes
        this.queue = [];               // File d'attente des requtes
        this.processing = false;       // Flag de traitement
    }

    /**
     * Ajoute une requte  la file d'attente
     */
    async execute(requestFunction) {
        return new Promise((resolve, reject) => {
            this.queue.push({
                request: requestFunction,
                resolve,
                reject
            });

            this.processQueue();
        });
    }

    /**
     * Traite la file d'attente des requtes
     */
    async processQueue() {
        if (this.processing || this.queue.length === 0) {
            return;
        }

        this.processing = true;

        while (this.queue.length > 0) {
            // Nettoyer les anciennes requtes
            const now = Date.now();
            this.requests = this.requests.filter(
                timestamp => now - timestamp < this.timeWindow
            );

            // Vrifier si on peut faire une nouvelle requte
            if (this.requests.length >= this.maxRequests) {
                // Attendre jusqu' ce qu'une requte sorte de la fentre
                const oldestRequest = Math.min(...this.requests);
                const waitTime = this.timeWindow - (now - oldestRequest);
                
                if (waitTime > 0) {
                    console.log(`Rate limiter: waiting ${waitTime}ms before next request`);
                    await this.delay(waitTime);
                    continue;
                }
            }

            // Excuter la prochaine requte
            const { request, resolve, reject } = this.queue.shift();
            this.requests.push(Date.now());

            try {
                const result = await request();
                resolve(result);
            } catch (error) {
                reject(error);
            }

            // Petit dlai entre les requtes pour viter de surcharger
            await this.delay(50);
        }

        this.processing = false;
    }

    /**
     * Utilitaire pour crer un dlai
     */
    delay(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    /**
     * Rinitialise le limiteur
     */
    reset() {
        this.requests = [];
        this.queue = [];
        this.processing = false;
    }

    /**
     * Obtient les statistiques actuelles
     */
    getStats() {
        const now = Date.now();
        const recentRequests = this.requests.filter(
            timestamp => now - timestamp < this.timeWindow
        ).length;

        return {
            queueLength: this.queue.length,
            recentRequests,
            maxRequests: this.maxRequests,
            canMakeRequest: recentRequests < this.maxRequests
        };
    }
}

// Instance globale du limiteur de dbit
// Limite  5 requtes par seconde pour tre conservateur
const globalRateLimiter = new RateLimiter(5, 1000);

/**
 * Fonction wrapper pour appliquer la limitation de dbit  une requte
 */
export const withRateLimit = async (requestFunction) => {
    return globalRateLimiter.execute(requestFunction);
};

