import { useState, useCallback, useEffect } from 'react';
import { apiEndpoints, handleApiError } from '../utils/api';

/**
 * Hook personnalis pour grer les appels API avec tat de chargement et gestion d'erreurs
 */
export const useApi = (apiFunction, dependencies = [], options = {}) => {
    const {
        immediate = true,
        showErrorMessage = true,
        onSuccess,
        onError
    } = options;

    //  Ajoutez cette vrification
    useEffect(() => {
        if (immediate && (!apiFunction || typeof apiFunction !== 'function')) {
            console.error(' apiFunction doit tre une fonction, reu :', apiFunction);
        }
    }, [apiFunction, immediate]);

    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const execute = useCallback(async (...args) => {
        try {
            setLoading(true);
            setError(null);
            
            const result = await apiFunction(...args);
            setData(result);
            
            if (onSuccess) {
                onSuccess(result);
            }
            
            return result;
        } catch (err) {
            setError(err);
            
            if (onError) {
                onError(err);
            } else {
                handleApiError(err, showErrorMessage);
            }
            
            throw err;
        } finally {
            setLoading(false);
        }
    }, [apiFunction, onSuccess, onError, showErrorMessage]);

    useEffect(() => {
        if (immediate && apiFunction) {
            execute();
        }
    }, [dependencies, execute, immediate, apiFunction]);

    const refetch = useCallback(() => execute(), [execute]);

    return {
        data,
        loading,
        error,
        execute,
        refetch
    };
};

/**
 * Hook pour les oprations CRUD avec gestion automatique des tats
 */
export const useCrudApi = (entityName, apiMethods) => {
    const [items, setItems] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [operationLoading, setOperationLoading] = useState({});

    // Charger tous les lments
    const loadItems = useCallback(async () => {
        try {
            setLoading(true);
            setError(null);
            const result = await apiMethods.getAll();
            setItems(Array.isArray(result) ? result : []);
        } catch (err) {
            setError(err);
            handleApiError(err);
        } finally {
            setLoading(false);
        }
    }, [apiMethods]);

    // Ajouter un lment
    const addItem = useCallback(async (data) => {
        try {
            setOperationLoading(prev => ({ ...prev, add: true }));
            setError(null);
            
            const newItem = await apiMethods.add(data);
            setItems(prev => [...prev, newItem]);
            
            if (window.Swal) {
                window.Swal.fire({
                    title: 'Succs',
                    text: `${entityName} ajout(e) avec succs`,
                    icon: 'success',
                    timer: 2000,
                    showConfirmButton: false
                });
            }
            
            return newItem;
        } catch (err) {
            setError(err);
            handleApiError(err);
            throw err;
        } finally {
            setOperationLoading(prev => ({ ...prev, add: false }));
        }
    }, [apiMethods, entityName]);

    // Mettre  jour un lment
    const updateItem = useCallback(async (id, data) => {
        try {
            setOperationLoading(prev => ({ ...prev, [`update_${id}`]: true }));
            setError(null);
            
            const updatedItem = await apiMethods.update(id, data);
            setItems(prev => prev.map(item => 
                item.id === id ? { ...item, ...updatedItem } : item
            ));
            
            if (window.Swal) {
                window.Swal.fire({
                    title: 'Succs',
                    text: `${entityName} modifi(e) avec succs`,
                    icon: 'success',
                    timer: 2000,
                    showConfirmButton: false
                });
            }
            
            return updatedItem;
        } catch (err) {
            setError(err);
            handleApiError(err);
            throw err;
        } finally {
            setOperationLoading(prev => ({ ...prev, [`update_${id}`]: false }));
        }
    }, [apiMethods, entityName]);

    // Supprimer un lment avec confirmation
    const deleteItem = useCallback(async (id) => {
        if (!window.Swal) {
            if (!window.confirm(`tes-vous sr de vouloir supprimer ce ${entityName} ?`)) {
                return;
            }
        } else {
            const result = await window.Swal.fire({
                title: 'Confirmation',
                text: `tes-vous sr de vouloir supprimer ce ${entityName} ?`,
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#d33',
                cancelButtonColor: '#3085d6',
                confirmButtonText: 'Oui, supprimer',
                cancelButtonText: 'Annuler'
            });

            if (!result.isConfirmed) {
                return;
            }
        }

        try {
            setOperationLoading(prev => ({ ...prev, [`delete_${id}`]: true }));
            setError(null);
            
            await apiMethods.delete(id);
            setItems(prev => prev.filter(item => item.id !== id));
            
            if (window.Swal) {
                window.Swal.fire({
                    title: 'Supprim',
                    text: `${entityName} supprim(e) avec succs`,
                    icon: 'success',
                    timer: 2000,
                    showConfirmButton: false
                });
            }
        } catch (err) {
            setError(err);
            handleApiError(err);
            throw err;
        } finally {
            setOperationLoading(prev => ({ ...prev, [`delete_${id}`]: false }));
        }
    }, [apiMethods, entityName]);

    // Charger les donnes au montage
    useEffect(() => {
        loadItems();
    }, [loadItems]);

    return {
        items,
        loading,
        error,
        operationLoading,
        loadItems,
        addItem,
        updateItem,
        deleteItem,
        refetch: loadItems
    };
};

/**
 * Hooks spcifiques pour chaque entit
 */
export const useTeachers = () => {
    return useCrudApi('enseignant', {
        getAll: apiEndpoints.getAllTeachers,
        add: apiEndpoints.addTeacher,
        update: apiEndpoints.updateTeacher,
        delete: apiEndpoints.deleteTeacher
    });
};

export const useStudents = (classId) => {
    const [students, setStudents] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [isOrdered, setIsOrdered] = useState(
        localStorage.getItem('isOrdonned') === 'true'
    );

    const loadStudents = useCallback(async () => {
        if (!classId) return;
        
        try {
            setLoading(true);
            setError(null);
            
            const result = isOrdered 
                ? await apiEndpoints.getOrderedStudents(classId)
                : await apiEndpoints.getStudentsByClass(classId);
                
            setStudents(Array.isArray(result) ? result : []);
        } catch (err) {
            setError(err);
            handleApiError(err);
        } finally {
            setLoading(false);
        }
    }, [classId, isOrdered]);

    const toggleOrder = useCallback(() => {
        const newValue = !isOrdered;
        setIsOrdered(newValue);
        localStorage.setItem('isOrdonned', newValue.toString());
    }, [isOrdered]);

    const deleteStudent = useCallback(async (id) => {
        if (!window.Swal) {
            if (!window.confirm('tes-vous sr de vouloir supprimer cet tudiant ?')) {
                return;
            }
        } else {
            const result = await window.Swal.fire({
                title: 'Confirmation',
                text: 'tes-vous sr de vouloir supprimer cet tudiant ?',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#d33',
                cancelButtonColor: '#3085d6',
                confirmButtonText: 'Oui, supprimer',
                cancelButtonText: 'Annuler'
            });

            if (!result.isConfirmed) {
                return;
            }
        }

        try {
            await apiEndpoints.deleteStudent(id);
            setStudents(prev => prev.filter(student => student.id !== id));
            
            if (window.Swal) {
                window.Swal.fire({
                    title: 'Supprim',
                    text: 'tudiant supprim avec succs',
                    icon: 'success',
                    timer: 2000,
                    showConfirmButton: false
                });
            }
        } catch (err) {
            handleApiError(err);
        }
    }, []);

    useEffect(() => {
        loadStudents();
    }, [loadStudents]);

    return {
        students,
        loading,
        error,
        isOrdered,
        loadStudents,
        toggleOrder,
        deleteStudent,
        refetch: loadStudents
    };
};

